@font-face {
	font-family: 'Rubik';
	src: local('Rubik Light'), local('Rubik-Light'), url('/src/fonts/rubik/Rubiklight.woff2') format('woff2'),
		url('/src/fonts/rubik/Rubiklight.woff') format('woff'), url('/src/fonts/rubik/Rubiklight.ttf') format('truetype');
	font-weight: 300;
	font-style: normal;
}
